#about_me{
    position: relative;
    padding-bottom: 180px;
    background-image: url('../img/test.jpg');
    background-repeat: no-repeat;
    background-position: center left;
    background-size: cover;

    @media screen and (max-width: $m) {
        background: none;
        padding-bottom: 100px;
    }

    h2{
        margin-top: 80px;
    }

    .image{
        margin-top: 70px;
        // margin-bottom: 60px;
        // position: relative;
        // display: block;
    }

    .text{
        position: relative;
        z-index: 20;
        margin-top: 150px;
        @media screen and (max-width: $m) {
            margin-top: 50px;
        }
    }

    .dotted-block{
        // top: -40px;
        bottom: -50px;
        right: -40px;
        width: 300px;
        height: 498px;
    }

}
